//字體來源管理設定
    // 字體樣式
        //Silom 字體

        @font-face {
            font-family: 'Silom';
            src: local("Silom");
            src: url('../fonts/Silom.ttf') format("opentype");
        }
        .fy-silom{
            font-family: 'Silom';
        }

        //思源黑體
        // @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');
        @font-face {
            font-family: 'Noto Sans TC', sans-serif;
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Black.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Bold.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Light.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Medium.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Regular.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Thin.otf') format("truetype");

        }
        
        .fy-noto {
            font-family: 'Noto Sans TC', sans-serif;
        // ---------
    // ---------字體樣式 結束
        }
// --------------------字體設定區結束


// -----變數設定-----
