    // 圖檔設定結束

// -----變數設定結束-----

    // 容器斷點設定
        // PC 
        @mixin pc {
            @media(min-width: 1200px){
                @content;
            }
        }
        // Table
        @mixin table {
            @media (min-width: 768px){
                @content;
            }
        }
    // ------------
    // 外觀
        // 顏色
        $turkish-rose: #907A7B;
        .text-rose {
            color: $turkish-rose  !important;
        }
        .bg-rose {
            background-color: $turkish-rose !important;
        }
        
        .bg-rose-hover {
            &:hover{
                background-color: lighten($turkish-rose, 15%) !important;
                color: #fff !important;
            }
        }
        .bg-rose-hover2 {
            &:hover, :active{
                background-color: $turkish-rose !important;
                color: #fff !important;
            }
        }

        .scale-hover {
            // position: relative;
            transition-duration: 0.1s;
            &:hover,:active {
                transform: scale(1.05);
                opacity: 85%;
            }
        }
        // ---------------
        // 圓角設定
        .rounded-sp {
            border-radius: 16px !important; 
        }
        .rounded-top-sp {
            border-top-left-radius: 16px;
            border-top-right-radius: 16px;
        }
        // ---------------------
        //按鈕設定
            // button-size
            .btn-md {
                padding: 12px 20px !important; 
                @include table {
                    padding: 12px 13px !important;
                }
                @include pc {
                    padding: 12px 48px !important;
                }
            }
            // ---------button-size end------


        // ---------------------
        // hover等效設定
        .hover-sp {
            &:hover {
                background-color: $turkish-rose !important;
                color: #F5F0E7 !important;
            }
        }

        .hover-sp2 {
                color: #707070 !important;
            &:hover {
                background-color: #C0A4A5 !important;
                border-color: #C0A4A5 !important;
                color: #fff !important;
            }
        }

        .hover-sp3 {
            color: #fff !important;
            &:hover {
                background-color: $turkish-rose !important;
                border-color: $turkish-rose !important;
                color: #fff !important;
            }
        }